.imageSliderItemWrapper {
    margin-left: 20px;
    width: 350px;
    height: 400px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    /* border: 2px solid red; */
    padding-top: 0;
    overflow: hidden;
}

.imageSliderItemWrapperMedium {
    width: 300px;
    height: 350px;
}

.imageSliderItemWrapperSmall {
    width: 250px;
    height: 250px;
}

.imageSliderItem {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    height: 100%;
    width: 100%;

    /* border: 4px solid teal; */
    display: flex;
}

.imageSliderItemMedium {

}

.imgAndReflectionDiv {
    /* display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column; */
    /* border: 2px solid sandybrown; */
    width: 100%;
    height: 100%;
    
    transition: 0.5s ease-out;
    /* transform: translateX(-50%); */
    position: absolute; 
    margin: 0 !important;
    padding: 0 !important;
}


.imgAndReflectionDivMedium img {
    width:  auto;
    height: 250px !important;
}

.imgAndReflectionDivSmall img {
    width:  auto;
    height: 200px !important;
}

.imgAndReflectionDivMedium .imgReflection {

}

.imgAndReflectionDiv:nth-child(1) {
    flex-basis: 100%;
    top: 0;
    left: 0;
}
.imgAndReflectionDiv:nth-child(2) {
    flex-basis: 0%;
    top: 0;
    left: 100%;
}

.slideLeft:nth-child(1) {
    transform: translateX(-100%)
    /* transition: 0.5s ease-in-out !important; */
    /* flex-basis: 0%; */
}
.slideLeft:nth-child(2) {
    transform: translateX(-100%)
    /* flex-basis: 100%; */
    /* transition: .3s ease-in-out !important; */

}

.imageSliderItem img {
    width:  auto;
    height: 300px;
    transition: 1s ease-in-out;
    transform: translateX(0%);
    /* border: 1px solid tomato; */
    position: relative;
    z-index: 5;
}

.imageSliderItem img[class~="150"] {
    height: 150px;
}

.imageSliderItem .imgReflection {
    transform: rotateY(0deg) rotateX(180deg);
    z-index: 5;
}

.imageSliderItem .imgReflectionWrapper {
    position: relative;
    /* border: 2px solid white; */
}
.imageSliderItem .imgReflectionWrapper::before {
    position: absolute;
    content: "";
    top: 0px;
    left: -1px;
    width: 110%;
    height: 100%;
    /* background-color: brown; */
    background: linear-gradient(to top, black 75%, transparent 170%);
    z-index: 100;
}

.slideLeft img {
    /* transform: translateX(-100%); */
}

.slideLeft .imgReflection {
    /* transform: rotateY(0deg) rotateX(180deg) translateX(-100%); */
}

.imageSliderItemWrapper button {
    position: absolute;
    top: 50%;
    right: 10%;
}

.cancelTransition {
    transition: 0s !important;
}