@import url(https://fonts.googleapis.com/css?family=ABeeZee|Allura|Italiana|Montserrat|Philosopher|Playball|Poiret+One|Quicksand|Sofia&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* ABeeZee|Allura|Italiana|Montserrat|Philosopher|Playball|Poiret+One|Quicksand|Sofia */

.navbarDiv {
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    margin: 0px 100px;
    /* border: 2px solid white; */
    flex-basis: 50%;
    padding-top: 20px;
}

.navbarDiv::before {
    display: none;
    position: absolute;
    content: "";
    width: 100%;
    height: 1px;
    bottom: -15px;
    left: 10%;
    -webkit-transition: 1s ease-in-out;
    transition: 1s ease-in-out;
    -webkit-transform: translate(-10%, -50%);
            transform: translate(-10%, -50%);
    max-width: 100%;
    /* max-width: 0%; */
    background-color: rgb(6, 36, 37);
    box-shadow: 
    0 0 5px 0 rgba(5, 30, 35, 0.3)
    /* , 0 2px 10px 0 rgba(255, 255, 255, 0.4) */
    /* , 0 -2px 10px 0 rgba(255, 255, 255, 0.4) */
    ;
    
}

.navbarItemsList {
    display: flex;
    list-style-type: none;
    padding: 0px;
    margin: 0px;
}

.navbarItemsList li {
    font-size: 25px;
    font-family: Poiret One;
    /* text-transform: uppercase; */
    color: rgb(206, 206, 206);
    -webkit-transition: 0.5s ease-in-out;
    transition: 0.5s ease-in-out;
    cursor: pointer;
    letter-spacing: 0px;
    font-weight: bold;
    opacity: 0;
    -webkit-transform: translateX(-20px);
            transform: translateX(-20px);
}

.navbarItemShow {
    opacity: 1 !important;
    -webkit-transform: unset !important;
            transform: unset !important;
}

.navbarItemsList li:not(:first-child) {
    margin-left: 30px;
}

.navbarItemsList li:hover {
    text-shadow: 
    0 0 5px rgb(99, 99, 99)
    /* , 0 1px 5px rgba(255, 255, 255, 0.2) */
    /* , 0 -1px 5px rgba(255, 255, 255, 0.2) */
    ;
}
.App {
  background-color: black;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-left: 50px;
  padding-right: 50px;
}

.sliderDiv {
  margin-top: 100px;
  width: 100%;
  /* border: 2px solid cyan; */
  height: 400px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
}
.imageSliderItemWrapper {
    margin-left: 20px;
    width: 350px;
    height: 400px;
    display: flex;
    justify-content: flex-start;
    align-items: flex-start;
    /* border: 2px solid red; */
    padding-top: 0;
    overflow: hidden;
}

.imageSliderItemWrapperMedium {
    width: 300px;
    height: 350px;
}

.imageSliderItemWrapperSmall {
    width: 250px;
    height: 250px;
}

.imageSliderItem {
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;

    height: 100%;
    width: 100%;

    /* border: 4px solid teal; */
    display: flex;
}

.imageSliderItemMedium {

}

.imgAndReflectionDiv {
    /* display: flex;
    align-items: center;
    justify-content: flex-start;
    flex-direction: column; */
    /* border: 2px solid sandybrown; */
    width: 100%;
    height: 100%;
    
    -webkit-transition: 0.5s ease-out;
    
    transition: 0.5s ease-out;
    /* transform: translateX(-50%); */
    position: absolute; 
    margin: 0 !important;
    padding: 0 !important;
}


.imgAndReflectionDivMedium img {
    width:  auto;
    height: 250px !important;
}

.imgAndReflectionDivSmall img {
    width:  auto;
    height: 200px !important;
}

.imgAndReflectionDivMedium .imgReflection {

}

.imgAndReflectionDiv:nth-child(1) {
    flex-basis: 100%;
    top: 0;
    left: 0;
}
.imgAndReflectionDiv:nth-child(2) {
    flex-basis: 0%;
    top: 0;
    left: 100%;
}

.slideLeft:nth-child(1) {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
    /* transition: 0.5s ease-in-out !important; */
    /* flex-basis: 0%; */
}
.slideLeft:nth-child(2) {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%)
    /* flex-basis: 100%; */
    /* transition: .3s ease-in-out !important; */

}

.imageSliderItem img {
    width:  auto;
    height: 300px;
    -webkit-transition: 1s ease-in-out;
    transition: 1s ease-in-out;
    -webkit-transform: translateX(0%);
            transform: translateX(0%);
    /* border: 1px solid tomato; */
    position: relative;
    z-index: 5;
}

.imageSliderItem img[class~="150"] {
    height: 150px;
}

.imageSliderItem .imgReflection {
    -webkit-transform: rotateY(0deg) rotateX(180deg);
            transform: rotateY(0deg) rotateX(180deg);
    z-index: 5;
}

.imageSliderItem .imgReflectionWrapper {
    position: relative;
    /* border: 2px solid white; */
}
.imageSliderItem .imgReflectionWrapper::before {
    position: absolute;
    content: "";
    top: 0px;
    left: -1px;
    width: 110%;
    height: 100%;
    /* background-color: brown; */
    background: -webkit-linear-gradient(bottom, black 75%, transparent 170%);
    background: linear-gradient(to top, black 75%, transparent 170%);
    z-index: 100;
}

.slideLeft img {
    /* transform: translateX(-100%); */
}

.slideLeft .imgReflection {
    /* transform: rotateY(0deg) rotateX(180deg) translateX(-100%); */
}

.imageSliderItemWrapper button {
    position: absolute;
    top: 50%;
    right: 10%;
}

.cancelTransition {
    -webkit-transition: 0s !important;
    transition: 0s !important;
}
